/* ===== VARIABLES CSS ===== */
:root {
  --primary-50: #eff6ff;
  --primary-100: #dbeafe;
  --primary-200: #bfdbfe;
  --primary-300: #93c5fd;
  --primary-400: #60a5fa;
  --primary-500: #3b82f6;
  --primary-600: #2563eb;
  --primary-700: #1d4ed8;
  --primary-800: #1e40af;
  --primary-900: #1e3a8a;
  
  --gray-50: #f9fafb;
  --gray-100: #f3f4f6;
  --gray-200: #e5e7eb;
  --gray-300: #d1d5db;
  --gray-400: #9ca3af;
  --gray-500: #6b7280;
  --gray-600: #4b5563;
  --gray-700: #374151;
  --gray-800: #1f2937;
  --gray-900: #111827;
  
  --white: #ffffff;
  --black: #000000;
  
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-10: 2.5rem;
  --space-12: 3rem;
  --space-16: 4rem;
  --space-20: 5rem;
  --space-24: 6rem;
  
  --gradient-primary: linear-gradient(135deg, var(--primary-600) 0%, var(--primary-800) 100%);
  --gradient-hero: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  
  --transition-all: all 0.3s ease;
  --transition-transform: transform 0.3s ease;
}

/* ===== RESET Y BASE ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  line-height: 1.6;
  color: var(--gray-800);
  background-color: var(--white);
}

/* ===== HERO SECTION ===== */
.hero-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-hero);
  color: white;
  overflow: hidden;
}

.hero-content {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-8);
  max-width: 1200px;
  margin: 0 auto;
  padding: var(--space-8) var(--space-4);
  width: 100%;
}

.hero-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-6);
  width: 100%;
}

@media (min-width: 768px) {
  .hero-profile {
    flex-direction: row;
    gap: var(--space-8);
    justify-content: center;
  }
}

.hero-photo {
  width: 160px;
  height: 160px;
  border-radius: 24px;
  object-fit: cover;
  border: 3px solid rgba(255, 255, 255, 0.2);
  box-shadow: var(--shadow-2xl);
  transition: var(--transition-all);
}

.hero-photo:hover {
  transform: translateY(-8px) scale(1.02);
}

.hero-text {
  text-align: center;
  flex: 1;
}

@media (min-width: 768px) {
  .hero-text {
    text-align: left;
  }
}

.hero-text h1 {
  font-size: clamp(1.75rem, 4vw, 2.5rem);
  font-weight: 700;
  margin-bottom: var(--space-2);
}

.hero-text .subtitle {
  font-size: clamp(1rem, 2.5vw, 1.25rem);
  font-weight: 500;
  margin-bottom: var(--space-4);
}

.hero-text .hero-description {
  font-size: clamp(0.9rem, 2vw, 1.1rem);
  line-height: 1.6;
  opacity: 0.9;
}

.hero-stats {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--space-4);
  flex-wrap: wrap;
}

.stat-item {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  padding: var(--space-4) var(--space-5);
  text-align: center;
  transition: var(--transition-all);
  min-width: 120px;
}

.stat-item:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.15);
}

.stat-number {
  display: block;
  font-size: clamp(1.5rem, 3vw, 2rem);
  font-weight: 800;
  margin-bottom: var(--space-1);
}

.stat-label {
  font-size: clamp(0.7rem, 1.5vw, 0.8rem);
  font-weight: 500;
  opacity: 0.8;
}

.hero-cta {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: var(--space-3);
}

.btn {
  padding: var(--space-3) var(--space-5);
  border-radius: 12px;
  text-decoration: none;
  font-weight: 500;
  transition: var(--transition-all);
  display: inline-block;
}

.btn:hover {
  transform: translateY(-2px);
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  border: none;
}

.btn-outline {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

/* ===== SECCIONES ===== */
.section {
  padding: var(--space-20) 0;
}

.section-alt {
  background-color: var(--gray-50);
}

.section-header {
  text-align: center;
  margin-bottom: var(--space-16);
}

.section-title {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 800;
  color: var(--gray-900);
  margin-bottom: var(--space-4);
}

.section-subtitle {
  font-size: 1.125rem;
  color: var(--gray-600);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--space-4);
}

/* ===== RESPONSIVE ===== */
@media (max-width: 768px) {
  .hero-stats {
    flex-direction: column;
    gap: var(--space-3);
  }
}

/* ===== LAYOUT PRINCIPAL ===== */
#content-wrapper {
  margin-top: 0;
}

/* ===== HEADER PROFESIONAL ===== */
.professional-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--gray-200);
  z-index: 1000;
  transition: var(--transition-all);
}

.professional-header.scrolled {
  background: rgba(255, 255, 255, 0.98);
  box-shadow: var(--shadow-md);
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--space-4);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
}

.header-brand {
  flex: 1;
}

.brand-link {
  text-decoration: none;
  color: var(--gray-900);
}

.brand-name {
  font-size: 1.25rem;
  font-weight: 700;
  margin: 0;
}

.brand-title {
  font-size: 0.875rem;
  color: var(--primary-600);
  margin: 0;
}

.header-nav {
  display: flex;
  align-items: center;
  gap: var(--space-6);
}

.nav-links {
  display: none;
  align-items: center;
  gap: var(--space-6);
}

@media (min-width: 768px) {
  .nav-links {
    display: flex;
  }
}

.nav-link {
  text-decoration: none;
  color: var(--gray-700);
  font-weight: 500;
  padding: var(--space-2) var(--space-3);
  border-radius: var(--space-2);
  transition: var(--transition-all);
}

.nav-link:hover {
  color: var(--primary-600);
  background: var(--primary-50);
}

.nav-cta {
  background: var(--gradient-primary);
  color: white !important;
}

.nav-cta:hover {
  background: var(--gradient-primary);
  transform: translateY(-1px);
}

.header-actions {
  display: none;
  align-items: center;
  gap: var(--space-3);
}

@media (min-width: 1024px) {
  .header-actions {
    display: flex;
  }
}

.header-btn {
  padding: var(--space-2) var(--space-4);
  border-radius: var(--space-2);
  text-decoration: none;
  font-size: 0.875rem;
  font-weight: 500;
  transition: var(--transition-all);
}

.header-btn-primary {
  background: var(--gradient-primary);
  color: white;
}

.header-btn-secondary {
  background: var(--gray-100);
  color: var(--gray-700);
}

.header-btn:hover {
  transform: translateY(-1px);
}

/* Mobile Menu */
.mobile-menu-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 30px;
  height: 30px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

@media (min-width: 768px) {
  .mobile-menu-btn {
    display: none;
  }
}

.hamburger-line {
  width: 20px;
  height: 2px;
  background: var(--gray-700);
  margin: 2px 0;
  transition: var(--transition-all);
}

.mobile-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border-bottom: 1px solid var(--gray-200);
  transform: translateY(-100%);
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-all);
}

.mobile-menu.active {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

.mobile-menu-content {
  padding: var(--space-4);
}

.mobile-nav-link {
  display: block;
  padding: var(--space-3);
  text-decoration: none;
  color: var(--gray-700);
  border-bottom: 1px solid var(--gray-100);
}

.mobile-nav-link:hover {
  background: var(--gray-50);
  color: var(--primary-600);
}

.mobile-actions {
  display: flex;
  gap: var(--space-2);
  margin-top: var(--space-4);
}

.mobile-btn {
  flex: 1;
  padding: var(--space-3);
  text-align: center;
  text-decoration: none;
  border-radius: var(--space-2);
  font-weight: 500;
}

.mobile-btn-primary {
  background: var(--gradient-primary);
  color: white;
}

.mobile-btn-secondary {
  background: var(--gray-100);
  color: var(--gray-700);
}

/* ===== CONTACTO Y GRIDS ===== */
.contact-section {
  background: white;
  border-radius: 1.5rem;
  padding: var(--space-8);
  box-shadow: var(--shadow-lg);
  margin: var(--space-8) 0;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-6);
  margin-top: var(--space-8);
}

.contact-item {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  padding: var(--space-4);
  background: var(--gray-50);
  border-radius: var(--space-3);
  transition: var(--transition-all);
}

.contact-item:hover {
  background: var(--primary-50);
  transform: translateY(-2px);
}

.contact-icon {
  font-size: 1.5rem;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-100);
  border-radius: 50%;
  flex-shrink: 0;
}

.contact-info {
  flex: 1;
}

.contact-label {
  font-size: 0.75rem;
  color: var(--gray-500);
  margin-bottom: var(--space-1);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.contact-value {
  font-weight: 600;
  color: var(--gray-900);
}

.contact-value a {
  color: var(--primary-600);
  text-decoration: none;
}

.contact-value a:hover {
  text-decoration: underline;
}

/* ===== SKILLS GRID ===== */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-8);
  margin-top: var(--space-12);
}

.skill-category {
  background: white;
  border-radius: 1rem;
  padding: var(--space-6);
  box-shadow: var(--shadow-md);
  transition: var(--transition-all);
  border: 1px solid var(--gray-200);
}

.skill-category:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl);
}

.category-header {
  display: flex;
  align-items: center;
  margin-bottom: var(--space-6);
}

.category-icon {
  font-size: 2rem;
  margin-right: var(--space-3);
}

.category-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--gray-900);
  margin: 0;
}

.skills-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skills-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-3) 0;
  border-bottom: 1px solid var(--gray-100);
}

.skills-list li:last-child {
  border-bottom: none;
}

.skill-name {
  font-weight: 500;
  color: var(--gray-800);
}

.skill-level {
  padding: var(--space-1) var(--space-3);
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.level-expert {
  background: var(--primary-100);
  color: var(--primary-800);
}

.level-advanced {
  background: #dcfce7;
  color: #166534;
}

.level-intermediate {
  background: #fef3c7;
  color: #92400e;
}

/* ===== PROFILE SECTION ===== */
.profile-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-8);
  margin: var(--space-12) 0;
}

@media (min-width: 768px) {
  .profile-section {
    flex-direction: row;
    align-items: flex-start;
  }
}

.profile-photo {
  width: 200px;
  height: 200px;
  border-radius: 1rem;
  object-fit: cover;
  box-shadow: var(--shadow-xl);
  flex-shrink: 0;
}

.profile-content {
  flex: 1;
  text-align: center;
}

@media (min-width: 768px) {
  .profile-content {
    text-align: left;
  }
}

.profile-intro {
  font-size: 1.125rem;
  color: var(--gray-600);
  margin-bottom: var(--space-2);
}

.profile-highlight {
  font-size: 2rem;
  font-weight: 800;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: var(--space-4);
}

/* ===== CARDS ===== */
.card {
  background: white;
  border-radius: 1rem;
  padding: var(--space-6);
  box-shadow: var(--shadow-md);
  transition: var(--transition-all);
  border: 1px solid var(--gray-200);
  height: 100%;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl);
}

.card-header {
  margin-bottom: var(--space-4);
}

.card-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--gray-900);
  margin-bottom: var(--space-2);
}

.card-subtitle {
  color: var(--primary-600);
  font-weight: 600;
  font-size: 0.875rem;
}

.card-body {
  margin-bottom: var(--space-4);
}

.card-body p {
  margin-bottom: var(--space-3);
  line-height: 1.6;
  color: var(--gray-700);
}

.card-body ul {
  list-style: none;
  padding: 0;
  margin: var(--space-3) 0;
}

.card-body li {
  position: relative;
  padding-left: var(--space-5);
  margin-bottom: var(--space-2);
  line-height: 1.5;
  color: var(--gray-700);
}

.card-body li:before {
  content: "▶";
  position: absolute;
  left: 0;
  top: 0;
  color: var(--primary-500);
  font-size: 0.75rem;
}

.card-footer {
  margin-top: auto;
}

.badge-group {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2);
}

.badge {
  padding: var(--space-1) var(--space-3);
  background: var(--primary-100);
  color: var(--primary-800);
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
}

/* ===== HOVER EFFECTS ===== */
.hover-lift {
  transition: var(--transition-all);
}

.hover-lift:hover {
  transform: translateY(-4px);
}

/* ===== TIMELINE ===== */
.timeline {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
}

.timeline:before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--primary-200);
  transform: translateX(-50%);
}

@media (max-width: 768px) {
  .timeline:before {
    left: 20px;
  }
}

.timeline-item {
  position: relative;
  margin-bottom: var(--space-12);
}

.timeline-item:last-child {
  margin-bottom: 0;
}

.timeline-marker {
  position: absolute;
  left: 50%;
  top: 0;
  width: 16px;
  height: 16px;
  background: var(--primary-600);
  border: 4px solid white;
  border-radius: 50%;
  transform: translateX(-50%);
  box-shadow: var(--shadow-md);
  z-index: 10;
}

@media (max-width: 768px) {
  .timeline-marker {
    left: 20px;
  }
}

.timeline-content {
  background: white;
  border-radius: 1rem;
  padding: var(--space-6);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--gray-200);
  margin-left: calc(50% + 30px);
  width: calc(50% - 30px);
  transition: var(--transition-all);
}

.timeline-item:nth-child(even) .timeline-content {
  margin-left: 0;
  margin-right: calc(50% + 30px);
}

@media (max-width: 768px) {
  .timeline-content {
    margin-left: 50px;
    margin-right: 0;
    width: calc(100% - 50px);
  }
  
  .timeline-item:nth-child(even) .timeline-content {
    margin-left: 50px;
    margin-right: 0;
  }
}

.timeline-content:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.timeline-date {
  color: var(--primary-600);
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: var(--space-2);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.timeline-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--gray-900);
  margin-bottom: var(--space-2);
}

.timeline-company {
  color: var(--primary-600);
  font-weight: 600;
  margin-bottom: var(--space-4);
}

.timeline-description p {
  margin-bottom: var(--space-3);
  line-height: 1.6;
  color: var(--gray-700);
}

.timeline-description ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.timeline-description li {
  position: relative;
  padding-left: var(--space-5);
  margin-bottom: var(--space-2);
  line-height: 1.5;
  color: var(--gray-700);
}

.timeline-description li:before {
  content: "▶";
  position: absolute;
  left: 0;
  top: 0;
  color: var(--primary-500);
  font-size: 0.75rem;
}

/* ===== FORMULARIOS ===== */
.contact-form {
  max-width: 600px;
  margin: 0 auto;
  background: white;
  padding: var(--space-8);
  border-radius: 1rem;
  box-shadow: var(--shadow-lg);
}

.form-group {
  margin-bottom: var(--space-6);
}

.form-group label {
  display: block;
  font-weight: 600;
  color: var(--gray-700);
  margin-bottom: var(--space-2);
  font-size: 0.875rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: var(--space-3);
  border: 2px solid var(--gray-200);
  border-radius: var(--space-2);
  font-size: 1rem;
  transition: var(--transition-all);
  background: white;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--space-4);
}

@media (min-width: 640px) {
  .form-row {
    grid-template-columns: 1fr 1fr;
  }
}

.form-submit {
  text-align: center;
  margin-top: var(--space-8);
}

.btn-submit {
  background: var(--gradient-primary);
  color: white;
  border: none;
  padding: var(--space-4) var(--space-8);
  border-radius: var(--space-2);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition-all);
  position: relative;
  overflow: hidden;
}

.btn-submit:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn-submit:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.form-success {
  background: #dcfce7;
  color: #166534;
  padding: var(--space-4);
  border-radius: var(--space-2);
  margin-top: var(--space-4);
  display: none;
}

.form-error-message {
  background: #fef2f2;
  color: #dc2626;
  padding: var(--space-4);
  border-radius: var(--space-2);
  margin-top: var(--space-4);
  display: none;
}

/* ===== UTILIDADES ===== */
.text-center { text-align: center; }
.text-muted { color: var(--gray-500); }
.mt-5 { margin-top: var(--space-20); }
.mb-4 { margin-bottom: var(--space-16); }
.d-flex { display: flex; }
.justify-center { justify-content: center; }
.align-center { align-items: center; }
.gap-4 { gap: var(--space-4); }
.w-full { width: 100%; }
.max-w-4xl { max-width: 56rem; }
.mx-auto { margin-left: auto; margin-right: auto; }

/* ===== ANIMACIONES ===== */
@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translateX(-50%) translateY(0);
  }
  40%, 43% {
    transform: translateX(-50%) translateY(-30px);
  }
  70% {
    transform: translateX(-50%) translateY(-15px);
  }
  90% {
    transform: translateX(-50%) translateY(-4px);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

.fade-in-up {
  animation: slideInUp 0.6s ease-out;
}

/* ===== RESPONSIVE FINAL ===== */
@media (max-width: 480px) {
  .container {
    padding: 0 var(--space-3);
  }
  
  .section {
    padding: var(--space-16) 0;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
    gap: var(--space-4);
  }
  
  .contact-grid {
    grid-template-columns: 1fr;
  }
}
